    body {
        margin: auto;
        /* max-width: 650px; */
        background-color: #f5f5f5;
    }
    
    #page {
        max-width: 650px;
        margin: auto;
    }
    
    .login {
        max-width: 450px;
        margin-left: auto;
        margin-right: auto;
    }
    
    #login-input {
        width: 400px;
        height: 15px;
        padding: 10px;
        margin: 10px;
        font-size: 16px;
    }
    
    button {
        padding: 10px;
        margin: 5px;
        margin-right: 15px;
        width: 170px;
        color: white;
        font-weight: bold;
        font-size: 17px;
        background-color: #4a20e4;
        border: none;
    }
    
    button:hover {
        background-color: blue;
    }
    
    td {
        border-bottom: 1px solid #ddd;
        padding: 10px;
        margin: 0 auto;
        word-break: break-word;
    }
    
    table {
        margin: 0 auto;
        width: 90%;
    }
    
    .table-admin {
        margin: 0 auto;
        width: 100%;
    }
    
    fieldset {
        padding: 10px;
        border-color: #ffd902;
        margin-top: 0.5cm;
        margin-bottom: 0.5cm;
        margin-right: 15px;
        margin-left: 15px;
    }
    
    legend {
        color: #0E0042;
    }
    
    p {
        text-align: center;
    }
    
    img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        height: 200px;
        width: 200px;
        margin-top: 0.5cm;
        margin-bottom: 1cm;
    }
    
    .modal {
        padding: 10px;
        margin-top: 20px;
        margin-bottom: 20px;
        margin-left: 20px;
        margin-right: 20px;
    }
    
    .mobile-input {
        padding: 10px;
        text-align: center;
        font-style: italic;
        font-size: 17px;
        width: 210px;
    }
    
    .dashboard {
        max-width: 100%;
        margin: auto;
        padding: 20px;
    }
    
    .chart {
        max-width: 300px;
        height: 300px;
        display: flex;
    }
    
    .admin-tablerow {
        text-align: center;
    }
    
    .date-input {
        width: 19px;
    }
    
    .username {
        margin-left: 90%;
        font-weight: bold;
        height: 20px;
        width: 20px;
        display: flex;
        font-size: 25px;
    }
    
    .img-logout {
        margin: 0px;
        padding-left: 10px;
        height: 30px;
        width: 30px;
        cursor: default;
    }
    
    .btn-search,
    .btn-reset {
        background-color: #FDB45C;
        padding: 7px;
        margin: 10px;
        border-radius: 15px;
        cursor: pointer;
    }
    
    .logout {
        background-color: #FDB45C;
        margin-right: auto;
        margin-left: 50%;
        cursor: pointer;
        border-radius: 12px;
        padding: 10px;
    }
    
    .login-button {
        background-color: #FDB45C;
        margin-left: 200px;
        max-width: 100px;
        cursor: pointer;
        padding: 10px;
    }
    
    .btn-admin {
        width: auto;
        display: inline;
        padding: 7px;
        background-color: #FDB45C;
        font-family: 'Lucida Sans';
    }
